

































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import * as EmailValidator from 'email-validator'

import { getEmailVerCode, captchaUrl, register } from '@/services/server'
import * as ERR_CODES from '@/constants/errCodes'
import * as ZHUGE_EVENTS from '@/constants/zhugeIOEvents'
import {
  REGISTER_INIT,
  REGISTER_SUCCESS,
  WEBVIEW_HEIGHT,
  LOAD_PROTOCOL,
} from '@/constants/accountMsg'
@Component({})
export default class Register extends Vue {
  curPage: 0 | 1 = 0
  registerEmail: string = ''
  registerCaptchaSrc: string = ''
  registerCaptcha: string = ''
  emailForRegister: string = ''
  registerVerCode: string = ''
  registerPassword: string = ''
  gettingVerCode: boolean = false
  registering: boolean = false
  didVisitedLastPage: boolean = false
  countdown: number = 60
  unGetEmail: boolean = true
  invitationCode: string = ''
  protocol: boolean = false
  // @ts-ignore
  countdownInterval: NodeJS.Timeout | null = null
  modalTitle: string = 'account.ui.register'
  mounted() {
    console.log('Register mounted')
    window.sendIpcMessage(REGISTER_INIT)
    this.refreshCaptcha()
    this.initPage()
  }

  initPage() {
    this.curPage = 0
    this.registerEmail = ''
    this.registerCaptcha = ''
    this.emailForRegister = ''
    this.registerVerCode = ''
    this.registerPassword = ''
    this.gettingVerCode = false
    this.registering = false
    this.$nextTick(() => {
      // must in $nextTick
      // @ts-ignore
      this.$refs['register-email-input'].focus()
    })
  }
  login() {
    this.$router.push('/')
  }
  get filled() {
    return (
      this.emailForRegister &&
      this.registerPassword &&
      this.registerVerCode &&
      this.protocol
    )
  }
  reGetEmailCode() {
    if (!this.countdown) {
      this.sendEmailVerCode()
    }
  }
  transfer() {
    window.sendIpcMessage(LOAD_PROTOCOL)
    return false
  }
  getDisplayPrompt() {
    return this.countdown ? `${this.countdown}秒 重新获取` : '重新获取'
  }

  startCountdown() {
    if (this.countdownInterval) {
      clearInterval(this.countdownInterval)
      this.countdownInterval = null
    }
    this.countdownInterval = setInterval(() => {
      if (this.countdown === 0 && this.countdownInterval) {
        clearInterval(this.countdownInterval)
        this.countdownInterval = null
        return
      }
      this.countdown--
    }, 1000)
  }

  refreshCaptcha() {
    this.registerCaptchaSrc = `${captchaUrl}?t=${Date.now()}`
    this.registerCaptcha = ''
  }

  sendEmailVerCode() {
    if (!EmailValidator.validate(this.registerEmail)) {
      this.$Message.error(this.$t('account.message.invalidEmail'))
      return
    }
    if (this.registerCaptcha.length === 0) {
      this.$Message.error(this.$t('account.message.noCaptcha'))
      return
    }
    this.gettingVerCode = true
    getEmailVerCode(this.registerEmail, this.registerCaptcha)
      .then(() => {
        this.unGetEmail = false
        this.emailForRegister = this.registerEmail
        this.gettingVerCode = false
        this.countdown = 60
        this.startCountdown()
        return true
      })
      .catch((err: any) => {
        this.gettingVerCode = false
        this.refreshCaptcha()
        if (err.code === ERR_CODES.SERVER_EMAIL_SEND_ERROR) {
          return this.$Message.error(this.$t('account.message.sendVerCodeError'))
        }
        if (err.code === ERR_CODES.SERVER_CAPTCHA_ERROR) {
          return this.$Message.error(this.$t('account.message.wrongCaptcha'))
        }
        if (
          err.code === ERR_CODES.SERVER_EMAIL_FORMAT_ERROR ||
          err.code === ERR_CODES.SERVER_EMAIL_ADDRESS_NONE
        ) {
          return this.$Message.error(this.$t('account.message.invalidEmail'))
        }
        if (err.code === ERR_CODES.SERVER_USER_EXIST_ERROR) {
          return this.$Message.error(this.$t('account.message.userExists'))
        }
        return this.$Message.error(this.$t('account.message.sendVerCodeError'))
      })
  }
  register() {
    if (this.registerVerCode.length === 0) {
      this.$Message.error(this.$t('account.message.wrongVerCode'))
      return
    }
    if (!EmailValidator.validate(this.emailForRegister)) {
      this.$Message.error(this.$t('account.message.invalidEmail'))
      return
    }

    if (this.registerPassword.length < 6 || this.registerPassword.length > 20) {
      this.$Message.error(this.$t('account.message.invalidPassword'))
      return
    }
    if (!this.protocol) {
      this.$Message.error('请勾选用户协议')
      return
    }
    this.registering = true
    register(
      this.emailForRegister,
      this.registerPassword,
      this.registerVerCode,
      this.invitationCode,
    )
      .then(() => {
        window.sendIpcMessage(REGISTER_SUCCESS)
        this.login()
        return true
      })
      .catch((err: any) => {
        this.registering = false
        if (err.code === ERR_CODES.SERVER_EMAIL_CODE_VALID_ERROR) {
          return this.$Message.error(this.$t('account.message.wrongVerCode'))
        }
        if (err.code === ERR_CODES.SERVER_EMAIL_CODE_EXPIRED) {
          this.curPage = 0
          return this.$Message.error(this.$t('account.message.expiredVerCode'))
        }
        if (err.code === ERR_CODES.SERVER_EMAIL_IS_NOT_SAME_ERROR) {
          this.curPage = 0
          return this.$Message.error(this.$t('account.message.wrongEmail'))
        }
        if (
          err.code === ERR_CODES.SERVER_EMAIL_FORMAT_ERROR ||
          err.code === ERR_CODES.SERVER_EMAIL_ADDRESS_NONE
        ) {
          this.curPage = 0
          return this.$Message.error(this.$t('account.message.invalidEmail'))
        }
        return this.$Message.error(this.$t('account.message.regFail'))
      })
  }
}
