











































































import { Component, Vue, Watch } from 'vue-property-decorator'
import * as EmailValidator from 'email-validator'

import { getEmailVerCode, captchaUrl, resetPassword } from '@/services/server'
import * as ERR_CODES from '@/constants/errCodes'
import { RESET_PWD_INIT, WEBVIEW_HEIGHT } from '@/constants/accountMsg'
@Component({})
export default class ResetPwd extends Vue {
  resetpwdEmail: string = ''
  emailForResetPwd: string = ''
  resetpwdVerCode: string = ''
  newPassword: string = ''
  resettingPwd: boolean = false
  resetPwdCaptchaSrc: string = ''
  resetPWdCaptcha: string = ''
  gettingVerCode: boolean = false
  countdown: number = 60
  unGetEmail: boolean = true
  // @ts-ignore
  countdownInterval: NodeJS.Timeout | null = null
  modalTitle: string = 'account.ui.resetpwd'
  mounted() {
    console.log('Reset pwd mounted')
    window.sendIpcMessage(RESET_PWD_INIT)
    this.initPage()
    this.refreshCaptcha()
  }
  login() {
    console.log(5)
    this.$router.push('/')
  }
  initPage() {
    console.log('init page')
    this.emailForResetPwd = ''
    this.resetpwdEmail = ''
    this.resetpwdVerCode = ''
    this.newPassword = ''
    this.gettingVerCode = false
    this.resettingPwd = false
    this.resetPWdCaptcha = ''
    this.$nextTick(() => {
      // must in $nextTick
      // @ts-ignore
      this.$refs['resetpwd-email-input'].focus()
    })
  }
  reGetEmailCode() {
    if (!this.countdown) {
      this.sendEmailVerCode()
    }
  }
  getDisplayPrompt() {
    return this.countdown ? `${this.countdown}秒 重新获取` : '重新获取'
  }
  startCountdown() {
    if (this.countdownInterval) {
      clearInterval(this.countdownInterval)
      this.countdownInterval = null
    }
    this.countdownInterval = setInterval(() => {
      if (this.countdown === 0 && this.countdownInterval) {
        clearInterval(this.countdownInterval)
        this.countdownInterval = null
        return
      }
      this.countdown--
    }, 1000)
  }
  refreshCaptcha() {
    this.resetPwdCaptchaSrc = `${captchaUrl}?t=${Date.now()}`
    this.resetPWdCaptcha = ''
  }
  sendEmailVerCode() {
    if (!EmailValidator.validate(this.resetpwdEmail)) {
      this.$Message.error(this.$t('account.message.invalidEmail'))
      return
    }
    if (this.resetPWdCaptcha.length === 0) {
      this.$Message.error(this.$t('account.message.noCaptcha'))
      return
    }
    this.gettingVerCode = true
    getEmailVerCode(this.resetpwdEmail, this.resetPWdCaptcha, true)
      .then(() => {
        this.unGetEmail = false
        this.emailForResetPwd = this.resetpwdEmail
        this.gettingVerCode = false
        this.countdown = 60
        this.startCountdown()
        return true
      })
      .catch((err: any) => {
        this.gettingVerCode = false
        this.refreshCaptcha()
        if (err.code === ERR_CODES.SERVER_EMAIL_SEND_ERROR) {
          return this.$Message.error(this.$t('account.message.sendVerCodeError'))
        }
        if (err.code === ERR_CODES.SERVER_CAPTCHA_ERROR) {
          return this.$Message.error(this.$t('account.message.wrongCaptcha'))
        }
        if (
          err.code === ERR_CODES.SERVER_EMAIL_FORMAT_ERROR ||
          err.code === ERR_CODES.SERVER_EMAIL_ADDRESS_NONE
        ) {
          return this.$Message.error(this.$t('account.message.invalidEmail'))
        }
        if (err.code === ERR_CODES.SERVER_USER_EXIST_ERROR) {
          return this.$Message.error(this.$t('account.message.userExists'))
        }
        return this.$Message.error(this.$t('account.message.sendVerCodeError'))
      })
  }

  resetPwd() {
    if (!EmailValidator.validate(this.resetpwdEmail)) {
      this.$Message.error(this.$t('account.message.invalidEmail'))
      return
    }
    if (this.resetpwdVerCode.length === 0) {
      this.$Message.error(this.$t('account.message.wrongVerCode'))
      return
    }
    if (this.newPassword.length < 6 || this.newPassword.length > 20) {
      this.$Message.error(this.$t('account.message.invalidPassword'))
      return
    }
    this.resettingPwd = true
    resetPassword(this.emailForResetPwd, this.newPassword, this.resetpwdVerCode)
      .then(() => {
        this.$Message.success(this.$t('account.message.resetPwdSucc'))
        this.resettingPwd = false
        this.login()
        return true
      })
      .catch((err: any) => {
        this.resettingPwd = false
        if (err.code === ERR_CODES.SERVER_EMAIL_CODE_VALID_ERROR) {
          return this.$Message.error(this.$t('account.message.wrongVerCode'))
        }
        if (err.code === ERR_CODES.SERVER_EMAIL_CODE_EXPIRED) {
          return this.$Message.error(this.$t('account.message.expiredVerCode'))
        }
        if (err.code === ERR_CODES.SERVER_RESET_PWD_ACCOUNT_NOTEXIST) {
          return this.$Message.error(this.$t('account.message.accountNotExist'))
        }
        if (err.code === ERR_CODES.SERVER_RESET_PWD_FAIL) {
          return this.$Message.error(this.$t('account.message.resetPwdFail'))
        }
        if (
          err.code === ERR_CODES.SERVER_EMAIL_FORMAT_ERROR ||
          err.code === ERR_CODES.SERVER_EMAIL_ADDRESS_NONE
        ) {
          return this.$Message.error(this.$t('account.message.invalidEmail'))
        }
        return this.$Message.error(this.$t('account.message.resetPwdFail'))
      })
  }
}
